import React from 'react'
import './ProductList.css'
import ProductListItem from './ProductListItem'

import products from './products'

const ProductList = ({
}) => {
    return (
        <div>
            <h1>Product list</h1>
            <div className="row">
                {
                    products.map(({
                          id,
                          name,
                          description,
                          type,
                          capacity,
                          price,
                          image,
                    }) => (
                        <div className="col-lg-6" key={id}>
                            <ProductListItem
                                id={id}
                                image={image}
                                name={name}
                                description={description}
                                type={type}
                                capacity={capacity}
                                price={price}
                            />
                        </div>
                    ))
                }
            </div>
        </div>
    )
};

export default ProductList