import React from 'react'
import {keys} from "lodash";
import products, {getProductsMap} from "../../App/Main/Products/products";
import CartProductListItem from './CartProductListItem'

const CartProductList = ({
    productsInCart,
    productsMap = getProductsMap(products),
    CartListItemComponent = CartProductListItem,
    removeProductFromCart,
    setProductQuantity,
}) => {
    return (
        <div>
            {
                keys(productsInCart).map((productsId) => (
                    <CartListItemComponent
                        key={productsId}
                        product={productsMap[productsId]}
                        productCount={productsInCart[productsId]}
                        removeProductFromCart={removeProductFromCart}
                        setProductQuantity={setProductQuantity}
                    />
                ))
            }
        </div>
    )
}


export default CartProductList