import React, { Component } from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'


import './ProductListItem.css'
import QuantityInput from '../../../features/Quantity/QuantityInput'

class ProductListItem extends Component {

    state = {
        productCount: 1,
    }

    static propTypes = {
        name: PropTypes.string.isRequired,
        description: PropTypes.string,
        type: PropTypes.string.isRequired,
        capacity: PropTypes.number.isRequired,
        price: PropTypes.number.isRequired,
        image: PropTypes.string,
    }

    onIncrementClick = () => {
        this.setState((prevState)=>({
            productCount: prevState.productCount + 1
        }))
    }

    onDecrementClick = () => {
        this.setState((prevState)=>({
            productCount: prevState.productCount - 1
        }))
    }

    renderProductQuantity () {
        return (
            <QuantityInput
                productCount={this.state.productCount}
                onIncrementClick={this.onIncrementClick}
                onDecrementClick={this.onDecrementClick}
                minCount={1}
            />
        )
    }

    onLikeChange = () => {
        if(this.props.isLiked) {
            this.props.onDisLikeClick(this.props.id)
        } else {
            this.props.onLikeClick(this.props.id)
        }
    }


    render() {
        const {
            id,
            name,
            description = "No description",
            type,
            capacity,
            price,
            image,
            addProductToCart,
            isLiked = false,
    } = this.props;
        return (
            <div className="product-list-item">
                <div className="product-image">
                    <img src={image} alt={name} title={name}/>
                    <button
                        className={isLiked ? 'like-button-active' : 'like-button-disabled'}
                        onClick ={() =>this.onLikeChange()}/>
                </div>
                <h2 className="product-title">{name}</h2>
                <div className="product-description">{description}</div>
                <div className="product-features">Type: {type}</div>
                <div className="product-features">Capacity: {capacity} GB</div>
                {this.renderProductQuantity()}
                <div className="product-price">Price: {price} $</div>
                <button className="btn-add-to-cart"
                     onClick={() => addProductToCart(id,this.state.productCount)}>Add to cart
                </button>
            </div>
        )
    }
}

const mapStateToProps = (state,props)=>{
    return {
        isLiked: state.likedProducts[props.id]
    }
}

const mapDispatchToProps = (dispatch) =>({
    onLikeClick: (id) => dispatch({
        type: "LIKE",
        id:id
    }),

    onDisLikeClick: (id) => dispatch({
        type: "DISLIKE",
        id:id
    }),

    addProductToCart: (id,count) => dispatch({
        type: 'ADD_PRODUCT_TO_CART',
        id: id,
        count: count,
    })
})


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ProductListItem)
